      <li
        class="m-menu"
        @mouseover="openContent(item)"
        @mouseleave="closeContent(item)"
        v-for="(item, index) in list"
        :key="index"
      >
        <router-link to="#" :class="{ select: item.active }">{{
          item.title
        }}</router-link>
        <div v-if="item.select" class="list">
          <ul>
            <li v-for="(v, i) in item.menu" :key="i">{{ v }}</li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
