    return {
      list: [
        {
          title: "",
          active: true,
          select: false,
          menu: []
        },
        {
          title: "",
          active: false,
          select: false,
          menu: [
            "Java",
            "",
            "Spring Boot",
            "Python",
            "Go",
            "Spring",
            "MySQL",
            "",
            "Redis",
            "",
            "",
            "JVM",
