<template>
	<div class="m-aside">
		<el-row>
			<el-col :span="24" class="title_one"> - juejin.im</el-col>
		</el-row>
		<el-row>
			<el-col :span="24" class="title_two"></el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input type="text" class="username" placeholder="" prefix-icon="el-icon-user" v-model="userName"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input type="password" class="password" placeholder="" prefix-icon="el-icon-lock" v-model="password"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-button type="primary" class="login"></el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<span class="zhuCe" @click="openLogon({ name: '', type: 'register' })"></span>
				<span class="forget" @click="openLogon({ name: '', type: 'reset' })"></span>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				userName: "",
				password: ""
			};
		},
		methods: {
			openLogon(value) {
				this.$store.commit("setPattern", {
					name: value.name,
					type: value.type
				});
				console.log(this.$store.state);
				document.body.style.overflow = "hidden";
			}
		}
	};
</script>

<style lang="scss">
	@import "../../assets/css/index/aside.scss";
</style>
